import React, {useState} from 'react';
import "./CoinRow.css"
import {ReactComponent as BSCLogo} from "../../components/images/BNB.svg";
import {ReactComponent as AvalancheLogo} from "../../components/images/Avalanche.svg";
import {ReactComponent as CronosLogo} from "../../components/images/Cronos.svg";
import {ReactComponent as PolygonLogo} from "../../components/images/Polygon.svg";
import {ReactComponent as SolanaLogo} from "../../components/images/Solana.svg";
import {ReactComponent as TronLogo} from "../../components/images/Tron.svg";
import {ReactComponent as ETHLogo} from "../../components/images/Ethereum.svg";
import {ReactComponent as AlgoLogo} from "../../components/images/Algorand.svg";
import ShieldIcon from '@mui/icons-material/Shield';
import FingerprintIcon from '@mui/icons-material/Fingerprint';
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import StarIcon from '@mui/icons-material/Star';
import {
    BrowserView,
    MobileView,
    isBrowser,
    isMobile,
  } from "react-device-detect";

function CoinRow({Data, rowID}) {
    const [LogoImage, setLogoImage] = useState(Data.TokenInfo.LogoImage);
    const [showVote, setShowVote] = useState(false);
    const [showDemo, setShowDemo] = useState(false);
    const [WatchlistLogo, setWatchListLogo] = useState('#999999');
    const [WatchListLogoIcon, setWLIcon] = useState(StarBorderIcon);

    const selectProject = (event)=>{
        if(event.target.id == 'details'){
            let voteElement = document.getElementById(Data.projectTag+"_details");
            voteElement.style.maxHeight = (showDemo) ? '0px' : '1800px';
            setShowDemo(!showDemo);
        }
        if(event.target.id == "Vote"){
            let voteElement = document.getElementById(Data.projectTag+"_vote");
            voteElement.style.height = (showVote) ? '0px' : '140px';
            setShowVote(!showVote);
        }
    }

    const logos = {
        "BSC" : BSCLogo,
        "Ethereum" : ETHLogo,
        "Polygon" : PolygonLogo,
        "Solana": SolanaLogo,
        "Cronos" : CronosLogo,
        "Avalanche" : AvalancheLogo,
        "Algorand" : AlgoLogo,
        "Tron" : TronLogo,
        "SelectChain" : BSCLogo
    }
    const ChainLogo = logos[Data.TokenInfo.chain];

    const chain_color = {
        "BSC" : "gold",
        "Ethereum" : "#5f658b",
        "Polygon" : "#8d42ea",
        "Solana": "#5e82d7",
        "Cronos" : "#0e37ff",
        "Avalanche" : "#e84142",
        "Algorand" : "brown",
        "Tron" : "red",
    }

    const getEvent = ()=>{
      let time;
      switch(Data.EventInfo.current_phase){
          case "Launch":
            time = DateString(Data.EventInfo.launch_time);
            break;
          case "Presale":
            time = DateString(Data.EventInfo.presale_time);
            break;
          case "Whitelist":
            time = "Start: " + DateString(Data.EventInfo.whitelist_start);
            break;
      }
      return [Data.EventInfo.current_phase + ":", time];
    }

    const DateString = (eventDate)=>{
        if(Data.EventInfo.cp_live) return "Live";
        let DateStr = "";
        let EDate = new Date(eventDate);
        let Year = EDate.getFullYear();
        let Month = EDate.getMonth();
        let Day = EDate.getDay();
        let date = EDate.getDate();
        let Hour = EDate.getHours();
        let Minutes = EDate.getMinutes();
        if(Hour < 10){
            Hour  = "0" + Hour;
        }
        if(Minutes < 10){
            Minutes ="0" + Minutes;
        }
        let monthes = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul","Aug", "Sep", "Oct", "Nov", "Dec"];
        let days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"];
        DateStr = date  +" "+monthes[Month] + " At " + " " + Hour+":"+Minutes;

        return DateStr;
      }

      const overWatchlistLogo = ()=>{
          if(WatchListLogoIcon == StarIcon) return;
          setWatchListLogo('gold');
      }

      const leaveWatchListLogo = ()=>{
        if(WatchListLogoIcon == StarIcon) return;
        setWatchListLogo('#999999');
      }

      const AddedToWatchList = ()=>{
          setWLIcon(WatchListLogoIcon == StarBorderIcon? StarIcon : StarBorderIcon);
      }
    
    return (
    <div className='Coin' style={{height : isMobile ? '350px' : '155px'}}>
        <div style={{display:'flex', alignItems:'center', justifyContent:'center'}}>
            <WatchListLogoIcon style={{fontSize:isMobile?'100px' : '50px', color:WatchlistLogo}} 
            onMouseEnter={overWatchlistLogo} 
            onMouseLeave={leaveWatchListLogo}
            onClick={AddedToWatchList}></WatchListLogoIcon>
        </div>
        <div className='CL_coinInfo'>
          <div className='CL_Logo'  
          style={{backgroundImage: `url(${process.env.PUBLIC_URL+LogoImage})`, backgroundPosition:'center',
          width : isMobile? '350px' : '155px',
          height : isMobile? '350px' : '155px'
          }}></div>
            <div className='CL_Row_Info'>
                <div style={{display:'flex', alignItems:'center'}}>
                    <span className='CR_Category' style={{fontSize:isMobile? '75px' : '25px'}}>{Data.projectInfo.category}</span>
                </div>
                <div style={{display:'flex', alignItems:'center'}}>
                    <span className='CR_Name' style={{fontSize:isMobile? '75px' : '33px'}}>{Data.TokenInfo.name}</span>
                    <span className='CR_Symbol' style={{fontSize:isMobile? '60px' : '25px', marginLeft:isMobile?'30px' : '15px'}}>${Data.TokenInfo.symbol}</span>
                </div>
                <div style={{display:'flex', alignItems:'center'}}>
                    <div className='CR_chainLogo' style={{width:isMobile? '80px' : '40px', height:isMobile? '90px' : '50px'}}>
                        <ChainLogo></ChainLogo>
                    </div>
                    {
                        Data.TokenInfo.auditLink != "" &&
                        <div>
                            <span style={{color:'blue'}}><VerifiedUserIcon style={{fontSize:isMobile? '90px' : '44px'}}></VerifiedUserIcon></span>
                        </div>
                    }
                    {
                        Data.projectInfo.kyc_link != "" &&
                        <div>
                            <span style={{color:'#00ff00'}}><FingerprintIcon style={{fontSize:isMobile? '90px' : '44px'}}></FingerprintIcon></span>
                        </div>
                    }
                </div>
            </div>
        </div>
        <div className='CoinRow_event'>
            <span style={{fontSize:isMobile? '80px' : '35px'}}>{getEvent()[0]}</span>
            <span style={{fontSize:isMobile? '50px' : '25px'}}>{getEvent()[1]}</span>
        </div>
    </div>
  )
}

export default CoinRow