import React, {useEffect, useState, useRef} from 'react';
import "./CoinList.css";
import CoinRow from './CoinRow';
import SearchIcon from '@mui/icons-material/Search';
import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import TokenDetails from './tokenDetails';
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";


function CoinList({coins, projects}) {
  let len = Object.keys(coins).length;
  let dispatch = useDispatch();
  const currentProject = useSelector(state=>state.currentProject, shallowEqual);
  const [currentRow, setCurrentRow] = useState(len == 0 ? null : "Project_0")

  const listSettings_selects = {
    "Sort" : ["Sort By", "Event Time", "Watchlist"],
    "Chain" : ["Chain", "All", "BSC", "Ethereum", "Polygon", "Avalanche", "Cronos", "Tron", "Algorand", "Solana"],
    "Event" : ["Event", "All", "Launch", "Presale", "Whitelist Contest"],
    "Utility" : ["Utility", "All", "Audit", "KYC", "Audit & KYC"]
  }

  const CoinRowClicked = (event)=>{ 
    let prRow = document.getElementById(currentRow);
    prRow.style.boxShadow = "none";
    prRow.parentElement.style.boxShadow = 'none';
    event.currentTarget.style.boxShadow = "0px 0px 5px blue";
    if(isMobile && event.currentTarget.id != prRow.id){
      event.currentTarget.parentElement.style.boxShadow = '0px 0px 10px white';
    }
    if(isMobile){
      let mobileRow = document.getElementById(event.currentTarget.id + "_mobile");
      mobileRow.style.maxHeight = mobileRow.style.maxHeight == '0px'? '3000px' : '0px';
      console.log('is mobile ' + event.currentTarget.id + "_mobile")
    }
    dispatch({type:'select', project : event.currentTarget.id});
    setCurrentRow(event.currentTarget.id);
  }

  useEffect(()=>{
    if(currentRow == null) return;
    dispatch({type:'select', project : currentRow});
  }, []);

  const headerHandler = (event)=>{
    let header_name = event.target.id;
    let DataBase = [];
    let projects = Object.keys(coins);
    for(let i = 0; i < len; i++){
      DataBase.push(coins[projects[i]]);
    }
    console.log("DataBase : " + JSON.stringify(DataBase));
    let option = event.target.options(event.target.selectedIndex).text;
    if(header_name == "Sort"){
      if(option == 'Event Time'){
        
      }
    }
  }

  return (
    <div className='coinList' id='coinList'>
      <div className='coins'>

        {/* Coin List Header */}
        <div className='CoinList_Header'>
            {Array(Object.keys(listSettings_selects).length).fill().map((_, i)=>{
              let keys = Object.keys(listSettings_selects);
              return <select id={keys[i]}>
                {
                  Array(listSettings_selects[keys[i]].length)
                  .fill()
                  .map((_, j)=>{
                    let options = listSettings_selects[keys[i]];
                    return <option value={options[j]}>{options[j]}</option>
                  })
                }
              </select>
            })}
            <div style={{display:'flex', alignItems:'center'}}>
              <input placeholder={'search by name'} className={'searchInput'}></input>
              <SearchIcon style={{fontSize:'50px', color:'#7e7e7e'}}></SearchIcon>
            </div>
        </div>
   
      {/* ==================Add Coin Form Main End ==================*/}
        {/*Coins List*/}
        <div className='coinsList' style={{gridTemplateColumns:isMobile? '100%': '53% 47%'}}>
          {len == 0 && 
          <div style={{width:'100%', display:'flex', justifyContent:'center', color:'#909090', fontSize:'30px'}}>- No Projects Yet -</div>}
            {len != 0 &&
              <div className='CL_TokensTable' style={{paddingLeft:'10px', maxHeight : isMobile? '100%': '1770px'}}>
                {
                Array(len).fill().map((_, i)=>{
                return <div key={'row'+i} id={coins["Project_"+i].projectTag+"_row"} style={{marginBottom:isMobile? '20px':'10px'}}>
                  <div className='rowDiv' style={{marginBottom:'10px'}} id={coins["Project_"+i].projectTag} onClick={CoinRowClicked}>
                    <CoinRow  key={"Project" + i} Data ={coins["Project_"+i]} rowID={i} />
                  </div>
                  {
                    isMobile && 
                    <div id={coins["Project_"+i].projectTag+"_mobile"} className='CL_td_mobile' style={{width:'100%', maxHeight:'0px'}}>
                      <TokenDetails Data={coins["Project_"+i]}></TokenDetails>
                    </div>
                  }
                </div>
                })
                } 
              </div>
            }
            {
              currentProject != null && !isMobile &&
              <div style={{width:'100%', height:'fit-content'}} id="tokenDetails">
                <TokenDetails Data={coins[currentProject]}></TokenDetails>
              </div>
            }
          </div>
      </div>
    </div>
  )
}
export default CoinList;