import React, { useState, useEffect, useContext } from "react";
import "./Header.css";
import {useDispatch, useSelector, shallowEqual} from 'react-redux';
import storageHandler from '../store/storageHandler';
import Timer from "../components/Timer";
import BallotIcon from '@mui/icons-material/Ballot';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import PaletteIcon from '@mui/icons-material/Palette';
import AddIcon from '@mui/icons-material/Add';
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import ForumIcon from '@mui/icons-material/Forum';
import PageviewIcon from '@mui/icons-material/Pageview';
import EmailIcon from '@mui/icons-material/Email';
import DocumentScannerIcon from '@mui/icons-material/DocumentScanner';
import MenuIcon from '@mui/icons-material/Menu';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';
import SearchIcon from '@mui/icons-material/Search';
import LoginIcon from '@mui/icons-material/Login';
import WidgetsIcon from '@mui/icons-material/Widgets';

import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";

function Header() {
  
  const [MenuLogo, setMenuLogo] = useState(MenuIcon);
  const headers = ["NFT MarketPlace", "Lottery", "Submit Coin", "Audit", "Community", "Safu Scanner", "Docs", "Contact Us"]
  
  const header_icons = {
    "NFT MarketPlace" : <PaletteIcon></PaletteIcon>,
    "Lottery" : <EmojiEventsIcon></EmojiEventsIcon>,
    "Submit Coin" : <AddIcon></AddIcon>,
    "Audit" : <VerifiedUserIcon></VerifiedUserIcon>,
    "Community" : <ForumIcon></ForumIcon>,
    "Safu Scanner" : <PageviewIcon></PageviewIcon>,
    "Docs" : <DocumentScannerIcon></DocumentScannerIcon>,
    "Contact Us" : <EmailIcon></EmailIcon>
  }

  const MenuClicked = ()=>{
    if(MenuLogo == MenuIcon){
      document.getElementById('phone_menu').style.opacity = '0.85';
      document.getElementById('phone_menu').style.zIndex = '4';
      document.getElementById('exit_menu').style.opacity = '1';
      document.getElementById('exit_menu').style.zIndex = '4';
      document.getElementById('coinList').style.opacity = 0.3;
      document.getElementById('coinList').style.pointerEvents = 'none';
    }else{
      document.getElementById('phone_menu').style.opacity = '0';
      document.getElementById('phone_menu').style.zIndex = '-10';
      document.getElementById('exit_menu').style.opacity = '0';
      document.getElementById('exit_menu').style.zIndex = '-10';
      document.getElementById('coinList').style.opacity = 1;
      document.getElementById('coinList').style.pointerEvents = 'all';
    }
    setMenuLogo(MenuLogo == MenuIcon? MenuOpenIcon : MenuIcon);
  }

  return (
    <div className="header" style={{minHeight:isMobile? '300px' : '175px'}}>
      
      <div style={{height:'fit-content', display:'flex', width:'100%', alignItems:'center', rowGap:'10px'}}>

          {/* {
            isMobile && 
            <div className="header_menu_phone" id="humber_icon" onClick={MenuClicked} style={{display:'flex', alignItems:'center', justifyContent:'center', marginLeft:'auto'}}>
              <MenuLogo style={{fontSize:'150px'}}></MenuLogo>
            </div>
          } */}
            <span id="header_title" style={{fontSize: isMobile? '130px' : '50px', cursor:'auto', marginRight:'20px'}}>
              Coinscope
            </span>
  
          {
            !isMobile && 
            <div id="navs" style={{display:'flex', minWidth:'0px', minHeight:'0px', overflowX:'auto'}}>
            {
              Array(headers.length).fill().map((_, i)=>{
                return <div className="header_nav">
                    {header_icons[headers[i]]}
                    <span style={{fontSize:'18px', marginLeft:'8px'}}>{headers[i]}</span>
                  </div>
              })
            }
          </div>
          }
          {/* {
            isMobile && 
            <div style={{marginLeft:'auto'}}>
              <input placeholder="Search Coins..." style={{width:'100%', fontSize:'60px', padding:'50px', borderRadius:'80px', background:'#1a1a1a'}}></input>
            </div>
          } */}

          {
            isMobile && 
            <div style={{display:'flex', marginLeft:'auto'}}>
              <button onClick={MenuClicked} style={{fontSize:'80px',
              padding:'20px 70px', backgroundColor:'#0c0c0c',
              color:'white', border:'none', borderRadius:'25px', height:'100%',
              display:'flex',alignItems:'center'}}>
                <LoginIcon style={{fontSize:'80px', marginRight:'10px'}}></LoginIcon>
                <span>Log In</span>
              </button>

              <button onClick={MenuClicked} style={{marginLeft:'20px', fontSize:'80px',
              padding:'20px 70px', backgroundColor:'#0c0c0c',
              color:'white', border:'none', borderRadius:'25px', height:'100%',
              display:'flex',alignItems:'center'}}>
                <WidgetsIcon style={{fontSize:'80px', marginRight:'10px'}}></WidgetsIcon>
                <span>Menu</span>
              </button>
            </div>
          }

          {/* {
            isMobile && 
            <div style={{display:'flex', alignItems:'center', marginLeft:'auto'}}>
              <input placeholder="Search Coins..." 
                style={{height:'80px', fontSize:'45px', marginRight:'5px'}}></input>
              <SearchIcon style={{fontSize:'80px', color:'blue'}}></SearchIcon>
            </div>
              
          } */}
      </div>
    </div>
  );
}

export default Header;
