function Audit() {
  return (
    <div className='Audit_mainDiv'>
        <div className='Audit_logoContainer'>
                Recieve a  
                <PolicyIcon style={{fontSize:'100px', color:'blue'}}></PolicyIcon>
                 Badget!
        </div>
        <div className='Audit_content'>
            <span style={{marginBottom:'5px'}}>To Submit An Audit Request You Can Contact Us Using One Of This Ways:</span>
            <div>
                <TelegramIcon style={{fontSize:'40px', color:'#20a3ec'}}></TelegramIcon>
                <span style={{marginLeft:'5px'}}><b style={{color:'#20a3ec'}}>Telegram</b>: @Rspb2021</span>
            </div>
            <div>
                <EmailIcon style={{fontSize:'40px', color:'#5e00d0'}}></EmailIcon>
                <span style={{marginLeft:'5px'}}><b style={{color:'#5e00d0'}}>Email</b>: roholah_samad@yahoo.com</span>
            </div>
        </div>
    </div>
  )
}

export default Audit

