import React, { useState, useRef, useEffect } from "react";
import "./Body.css";
import {useSelector, shallowEqual} from "react-redux";
import CoinList from "./Coin/CoinList";
import AddCoinSlider from "./AddCoinSlider";
import Lottery from "./Lottery/Lottery";
import NFT from "./NFT/NFT";
import Audit from "./Audit/Audit";
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import PaletteIcon from '@mui/icons-material/Palette';
import AddIcon from '@mui/icons-material/Add';
import ForumIcon from '@mui/icons-material/Forum';
import PageviewIcon from '@mui/icons-material/Pageview';
import EmailIcon from '@mui/icons-material/Email';
import DocumentScannerIcon from '@mui/icons-material/DocumentScanner';
import BallotIcon from '@mui/icons-material/Ballot';
import TelegramIcon from '@mui/icons-material/Telegram';
import TwitterIcon from '@mui/icons-material/Twitter';
import AdUnitsIcon from '@mui/icons-material/AdUnits';
import CloseIcon from '@mui/icons-material/Close';

import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";

function Body() {
  let ProjectDB = useSelector(state=>state.AddProject, shallowEqual);
  let promoteds = useSelector(state=>state.promoteds, shallowEqual);
  let selectedDiv = useRef(null);
  const currentPage  = useRef('Tokens');
  const [selectedPage, setSelectedPage] = useState(<CoinList coins={ProjectDB}></CoinList>);

  const headers = ["NFT MarketPlace", "Lottery", "Submit Coin", "Audit", "Community", "Safu Scanner", "Docs", "Contact Us"]
  const menuSections = {
    "Menu" : [],
    "Sections" : ["Tokens", "NFT MarketPlace", "Lottery"],
    "Submission" : ["Submit Coin", "Audit", "Advertisement"],
    "Information" : ["Docs", "Safu Scanner"],
    "Join us" : ["Telegram", "Twitter"]
  }
  const SH = ["Sections", "Submission", "Information", "Join us"];

  const header_icons = {
    "Tokens" : <BallotIcon style={{fontSize:'70px'}}></BallotIcon>,
    "NFT MarketPlace" : <PaletteIcon style={{fontSize:'70px'}}></PaletteIcon>,
    "Lottery" : <EmojiEventsIcon style={{fontSize:'70px'}}></EmojiEventsIcon>,
    "Submit Coin" : <AddIcon style={{fontSize:'70px'}}></AddIcon>,
    "Audit" : <VerifiedUserIcon style={{fontSize:'70px'}}></VerifiedUserIcon>,
    "Community" : <ForumIcon style={{fontSize:'70px'}}></ForumIcon>,
    "Safu Scanner" : <PageviewIcon style={{fontSize:'70px'}}></PageviewIcon>,
    "Docs" : <DocumentScannerIcon style={{fontSize:'70px'}}></DocumentScannerIcon>,
    "Contact Us" : <EmailIcon style={{fontSize:'70px'}}></EmailIcon>,
    "Telegram" : <TelegramIcon style={{fontSize:'70px'}}></TelegramIcon>,
    "Twitter" : <TwitterIcon style={{fontSize:'70px'}}></TwitterIcon>,
    "Advertisement" : <AdUnitsIcon style={{fontSize:'70px'}}></AdUnitsIcon>
  }

  const handlePage = (event)=>{
    if(selectedDiv.current != null){
      if(event.currentTarget.id == selectedDiv.current.id) return;
      selectedDiv.current.classList.toggle('Body_Header_selected_Effect');
    }
    let elem = document.getElementById(event.currentTarget.id);
    elem.classList.toggle('Body_Header_selected_Effect');
    selectedDiv.current = elem;
    currentPage.current = event.currentTarget.id;
    switch(event.currentTarget.id){
      case "Tokens List":
        setSelectedPage(<CoinList coins={ProjectDB}></CoinList>);
        break;
      case "Lottery":
        setSelectedPage(<Lottery></Lottery>)
        break;
      case "Add Coin":
        setSelectedPage(<AddCoinSlider></AddCoinSlider>)
        break;
      case "Audit":
        setSelectedPage(<Audit></Audit>)
        break;
      case "NFT":
        setSelectedPage(<NFT></NFT>)
        break;
    }
  }

 const exitClicked = ()=>{
  document.getElementById('phone_menu').style.opacity = '0';
  document.getElementById('phone_menu').style.zIndex = '-10';
  document.getElementById('exit_menu').style.opacity = '0';
  document.getElementById('exit_menu').style.zIndex = '-10';
  document.getElementById('coinList').style.opacity = 1;
  document.getElementById('coinList').style.pointerEvents = 'all';
 }

  return (
    <div className="Body">
      {
        isMobile && 
        <div className="Body_Menu_phone" id="phone_menu">
            <ul className="Body_Menu_list">
              {
                Array(SH.length).fill().map((_, i)=>{
                  return <li style={{display:'flex', flexDirection:'column', alignItems:'start', marginBottom:'100px'}}>
                    <span style={{color:'#a3a3a3', fontSize:'90px'}}>
                      {SH[i]}
                    </span>
                    <ul>
                      {
                        Array(menuSections[SH[i]].length).fill().map((_, j)=>{
                          return <li className="Body_menu_row">
                            {header_icons[menuSections[SH[i]][j]]}
                            {menuSections[SH[i]][j]}
                          </li>})
                      }
                    </ul>
                    </li>
                })
              }
            </ul>
        </div>
      }
      {
        isMobile && 
        <div id="exit_menu" style={{position:'absolute', zIndex:'-5', opacity:'0', left:'85%', top:'1%'}} onClick={exitClicked}>
          <CloseIcon style={{fontSize:'240px'}}></CloseIcon>
        </div>
      }

      <div className="Body_List_Part" id="ListPart">
        <div style={{display:'flex', flexDirection:'column'}}>
          {selectedPage}
        </div>               
      </div>
    </div>
  );
}

export default Body;