import React from 'react'
import "./NFT.css";
import ConstructionIcon from '@mui/icons-material/Construction';

function NFT() {
  return (
    <div className='NFT_mainDiv'>
        <div className='NFT_developmentLogo'>
            <ConstructionIcon style={{fontSize:'100px', color:'blue'}}></ConstructionIcon>
        </div>
        <span>
            Still Under Development
        </span>
    </div>
  )
}

export default NFT