import React, {useState, useEffect, useRef} from 'react';
import "./Timer.css";
import ModeNightIcon from '@mui/icons-material/ModeNight';
import WbSunnyIcon from '@mui/icons-material/WbSunny';

function Timer() {
  
  const [clockState, setClockState] = useState([0, 0, 0, 'NaN']);
  const [TimeImage, setTimeImage] = useState(WbSunnyIcon);
  const componentMounted = useRef(true);

    useEffect(()=>{
        setInterval(()=>{
            var hour = (new Date()).getUTCHours();
            var minute = (new Date()).getUTCMinutes();
            var seconds = (new Date()).getUTCSeconds();
            if (hour < 10){
                hour = '0'+hour;
            }
            if (minute < 10){
                minute = '0' + minute;
            }
            if (seconds < 10){
                seconds = '0' + seconds;
            }
            if(componentMounted.current){
                setClockState([hour, minute, seconds]);
             }
        }, 1000);
        return ()=>{componentMounted.current = false;}
    }, []);

  return (
    <div className='Timer'>
        <span>{clockState[0]}:</span>
        <span>{clockState[1]}:</span>
        <span>{clockState[2]} </span>
        <span style={{fontSize:'20px', height:'100%', display:'flex', color:'rgb(204, 204, 204) ', marginLeft:'5px'}}>{"UTC"}</span>
    </div>
  )
}

export default Timer