const storageHandler =  () => ({
    saveTo : (state, data) => {
        try{
            localStorage.setItem(state, JSON.stringify(data));
        }catch(e){
            console.log(e);
        }
    }, 
    loadFrom : (state) => {
        try{
            const data = localStorage.getItem(state);
            return data ? JSON.parse(data) : undefined;
        }catch(e){
            return undefined;
        }
    },
    clearStorage : ()=>{
        localStorage.clear();
    }})

    
export default storageHandler;