import React, {useState, useEffect} from 'react'
import './tokenDetails.css';
import {ReactComponent as BSCLogo} from "../../components/images/BNB.svg";
import {ReactComponent as AvalancheLogo} from "../../components/images/Avalanche.svg";
import {ReactComponent as CronosLogo} from "../../components/images/Cronos.svg";
import {ReactComponent as PolygonLogo} from "../../components/images/Polygon.svg";
import {ReactComponent as SolanaLogo} from "../../components/images/Solana.svg";
import {ReactComponent as TronLogo} from "../../components/images/Tron.svg";
import {ReactComponent as ETHLogo} from "../../components/images/Ethereum.svg";
import {ReactComponent as AlgoLogo} from "../../components/images/Algorand.svg";
import Pie_chart from './Pie_chart';
import { PieChart, Pie, Sector, ResponsiveContainer } from 'recharts';
import WarningIcon from '@mui/icons-material/Warning';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { shallowEqual, useSelector } from 'react-redux';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import StarIcon from '@mui/icons-material/Star';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import {
    BrowserView,
    MobileView,
    isBrowser,
    isMobile,
  } from "react-device-detect";

function TokenDetails({Data}) {
    const [activeIndex, setActiveIndex] = useState(0);
    const currentEvent = Data.EventInfo.current_phase.toLowerCase();
    const [selectedDot, setDot] = useState(currentEvent + "_dot_"+Data.projectTag);
    const [selectedPhase, setSelectedPhase] = useState(Data.EventInfo.current_phase.toLowerCase());
    const currentProject = useSelector(state=>state.currentProject, shallowEqual);
    const [WatchListLogo, setWatchListLogo] = useState(StarBorderIcon);
    const [watchListCount, setWatchListCount] = useState(0);
    let analysis_data;
    const calculatePercents = ()=>{
        //presale, team, lp, locked, burnt
        let Percents = [
            parseFloat(Data.TokenInfo.presale),
            parseFloat(Data.TokenInfo.team),
            parseFloat(Data.TokenInfo.lp),
            parseFloat(Data.TokenInfo.locked),
            parseFloat(Data.TokenInfo.burn)
        ];
        let total = 0;
        for(let i = 0; i < Percents.length; i++){
          total += Percents[i];
        }
        let data = [
          { name: "Unlocked", value: (100 - total), fill:'orange'},
          { name: "Locked", value: Percents[3], fill:'#16db7e' },
          { name: "Burnt", value: Percents[4], fill:'brown' },
          { name: "Team", value: Percents[1], fill:'blue' },
          { name: "Presale", value: Percents[0], fill:'#8145a4' },
          { name: "Liquidity", value: Percents[2], fill:'#0c94c7' },
        ];
        return data;
      }

    const logos = {
        "BSC" : BSCLogo,
        "Ethereum" : ETHLogo,
        "Polygon" : PolygonLogo,
        "Solana": SolanaLogo,
        "Cronos" : CronosLogo,
        "Avalanche" : AvalancheLogo,
        "Algorand" : AlgoLogo,
        "Tron" : TronLogo,
        "SelectChain" : BSCLogo
    }

    const chain_color = {
        "BSC" : "gold",
        "Ethereum" : "#5f658b",
        "Polygon" : "#8d42ea",
        "Solana": "#5e82d7",
        "Cronos" : "#0e37ff",
        "Avalanche" : "#e84142",
        "Algorand" : "brown",
        "Tron" : "red",
    }

    const ChainLogo = logos[Data.TokenInfo.chain];
    const tokenomics = calculatePercents();

    const onPieEnter = (_, index) => {
        setActiveIndex(index);
      };

    const getDetails = (sp)=>{
        sp = sp.toLowerCase();
        let details = "";
        let events = ["Whitelist" , "Presale", "Launch"];
        let cp = Data.EventInfo.current_phase;
        let cpIndex = events.indexOf(cp);
        let cp_live = Data.EventInfo.cp_live;
        let statuses = {
            "Launch" : "",
            "Presale" : "",
            "Whitelist" : ""
        }
        for(let i = 0; i < events.length; i++){
            if(cpIndex > i){
                statuses[events[i]] = "Ended";
            }
            if(cpIndex == i){
                if(cp_live){
                    statuses[events[i]] = "Live";
                }else{
                    statuses[events[i]] = "Upcoming";
                }
            }
            if(cpIndex < i){
                statuses[events[i]] = "Upcoming";
            }
        }
        switch(sp.toLowerCase()){
            case "launch":
              details = {
                  "Buy Tax" : Data.EventInfo.buyTax,
                  "Sell Tax" : Data.EventInfo.sellTax,
                  "Had Presale" : Data.EventInfo.hadPresale == true && Data.EventInfo.presale_box == true? "Yes" : "No",
                  "Status" : statuses["Launch"]
              };
              break;
            case "presale":
                details = {
                    "Soft Cap" : Data.EventInfo.softCap,
                    "Hard Cap" : Data.EventInfo.hardCap,
                    "Whitelisted" : Data.EventInfo.whitelisted == true? "Yes" : "No",
                    "Open To Public" : Data.EventInfo.openToPublic == true? "Yes" : "No",
                    "Status" : statuses["Presale"]
                };
                break;
            case "whitelist":
                details = {
                    "Start" : Data.EventInfo.whitelist_start,
                    "End" : Data.EventInfo.whitelist_end,
                    "Status" : statuses["Whitelist"]
                };
                break;
        }
        return details;
    }

    const configureTimeline = (sDot)=>{
        let events = ['whitelist', 'presale', 'launch'];
        events.forEach(i=>{
            let text_dot = document.getElementById(i + "_dot_text_"+Data.projectTag);
            let dot = document.getElementById(i + "_dot_"+Data.projectTag);
            if(Data.EventInfo[i + "_box"] == false){
                text_dot.style.pointerEvents = 'none';
                text_dot.style.opacity = '0.3';
                dot.style.pointerEvents = 'none';
            }else{
                text_dot.style.pointerEvents = 'all';
                text_dot.style.opacity = '1';
                dot.style.pointerEvents = 'all';
            }
            dot.style.backgroundColor = 'black';
        })
        let sd = document.getElementById(sDot);
        sd.style.backgroundColor = "blue";
    }

    const event_dot_clicked = (event)=>{
        if(selectedDot == event.target.id) return;
        let events = ['whitelist', 'presale', 'launch'];
        let sd = document.getElementById(selectedDot);
        sd.style.backgroundColor = "black";
        event.target.style.backgroundColor = 'blue';
        setDot(event.target.id);
        for(let i =0; i < events.length; i++){
            if(event.target.id.includes(events[i])){
                setSelectedPhase(events[i])
            }
        }
    }

    const setAudit = ()=>{
        let analysis = {
            'Owner is able to set buy and sell limit to even 0' : "warning",
            'Owner is able to pause trade' : "warning",
            'Owner is able to exclude and include from blacklisted' : "warning",
            'No mint function found' : "ok",
            'Dev cant set taxes more than 25%' : "ok"
        }
        let final_analysis_data = [];
        let ak = Object.keys(analysis);
        ak.forEach(a=>{
            if(analysis[a] == "ok"){
                final_analysis_data.push(
                    <div style={{display:'flex', alignItems:'center'}}>
                        <CheckCircleIcon style={{color:'blue', fontSize:'40px'}}></CheckCircleIcon>
                        <span>{a}</span>
                    </div>
                )
            }else{
                final_analysis_data.push(
                    <div style={{display:'flex', alignItems:'center'}}>
                        <WarningIcon style={{color:'yellow', fontSize:'40px'}}></WarningIcon>
                        <span>{a}</span>
                    </div>
                )
            }
        });
        return final_analysis_data;
    }

    useEffect(()=>{
        setDot(currentEvent + "_dot_" + Data.projectTag);
        setSelectedPhase(Data.EventInfo.current_phase.toLowerCase());
        configureTimeline(currentEvent + "_dot_" + Data.projectTag);
        setWatchListLogo(StarBorderIcon);
        setWatchListCount(0);
        analysis_data = setAudit();
    }, [currentProject])

    const addToWatchList = ()=>{
        setWatchListLogo(WatchListLogo == StarBorderIcon? StarIcon : StarBorderIcon);
        setWatchListCount(watchListCount == 1? 0 : 1);
    }

  return (
    <div className='TD_mainDiv'>
        {!isMobile && 
        
        <div style={{display:'flex', alignItems:'center'}}>
            <div className='TD_Logo'  style={{backgroundImage: `url(${process.env.PUBLIC_URL+Data.TokenInfo.LogoImage})`, backgroundPosition:'center'}}></div>
            <div className='CL_Row_Info'>
                <div className='TD_watchList' onClick={addToWatchList}>
                    <WatchListLogo style={{fontSize:'35px'}}></WatchListLogo>
                    <span> Watchlist</span>
                </div>
                <span style={{fontSize:'28px'}}>{Data.TokenInfo.name}</span>
                <span style={{fontSize:'25px', color:'#898787'}}>${Data.TokenInfo.symbol}</span>
                <span style={{fontSize:'25px'}} className={'TD_watchListCount'}>
                    {watchListCount} Whatchlists
                </span>
            </div>
            <div className='TD_auditKYCChain'>
                {
                    Data.TokenInfo.auditLink && 
                    <span>
                        <a  style={{color:'blue'}} href={
                            !Data.TokenInfo.auditLink.includes("https://")?
                                "https://" + Data.TokenInfo.auditLink : Data.TokenInfo.auditLink
                            }>Audit</a>
                    </span>
                }
                {
                    Data.projectInfo.kyc_link && 
                    <span>
                        <a style={{color:'#00ff00'}} href={
                            !Data.projectInfo.kyc_link.includes("https://")?
                                "https://" + Data.projectInfo.kyc_link : Data.projectInfo.kyc_link
                            }>KYC</a>
                    </span>
                }
                <span style={{color:chain_color[Data.TokenInfo.chain]}}>
                    {Data.TokenInfo.chain}-
                </span>
            </div>
        </div>
            }
        <span className='TD_projectDetails' style={{height:isMobile? '350px' : '175px'}}>{Data.projectInfo.projectDetails}</span>
        <div className='TD_social' style={{marginTop: isMobile? '50px' : '15px'}}>
            {
                Array(Object.keys(Data.SocialInfo).length).fill().map((_, i)=>{
                    return <div style={{
                        opacity : Data.SocialInfo[Object.keys(Data.SocialInfo)[i]] == ""? "0.5" : "1",
                        fontSize:isMobile ? '80px' : '25px'
                        }}>
                            <a style={{
                                pointerEvents : Data.SocialInfo[Object.keys(Data.SocialInfo)[i]] == ""? "none" : "all"
                            }}
                            href={
                                !Data.SocialInfo[Object.keys(Data.SocialInfo)[i]].includes("https://")?
                                    "https://" + Data.SocialInfo[Object.keys(Data.SocialInfo)[i]] :
                                    Data.SocialInfo[Object.keys(Data.SocialInfo)[i]]
                            }>{Object.keys(Data.SocialInfo)[i]}</a>
                        </div>
                })
            }
        </div>
        <div className='TD_tokenomicsContainer'>
            {!isMobile &&
                <div className='TD_tokenomicsText'>
                    <span className='TD_tokenomicsTitle' style={{fontSize:isMobile? '80px' : '35px'}}>Tokenomics</span>
                    {
                        Array(6).fill().map((_, i)=>{
                            return <span className='TD_tokenomicsPercent' style={{fontSize:isMobile? '70px' : '23px'}}>{tokenomics[i].name} : {tokenomics[i].value}%</span>
                        })
                    }
                </div>
            }
            <div style={{width:isMobile? '0%' : '30%', display:'flex', flexDirection:'column'}}>
                {/* <span>Tokenomics</span>
                {
                    Array(6).fill().map((_, i)=>{
                        return <span className='TD_tokenomicsPercent' style={{fontSize:isMobile? '70px' : '23px'}}>{tokenomics[i].name} : {tokenomics[i].value}%</span>
                    })
                } */}
            </div>
            {
                isMobile && <div style={{width:'100%', display:'flex', flexDirection:'column', alignItems:'center'}}>
                    <div className='TD_tokenInfo_m'>
                        <span>Contract Address:</span>
                        <span>{Data.TokenInfo.contractAddress}</span>
                        <span>Total Supply:</span>
                        <span>{Data.TokenInfo.totalSupply}</span>
                        <span>Decimals : {Data.TokenInfo.decimals}</span>
                    </div>
                </div>
            }
            {!isMobile &&
                <div className='TD_pieChartContainer' style={{height:isMobile? '656px' : '275px'}}>
                    <ResponsiveContainer width="100%">
                    <PieChart>
                        <Pie
                        activeIndex={activeIndex}
                        activeShape={Pie_chart}
                        data={tokenomics}
                        cx="50%"
                        cy="50%"
                        innerRadius={60}
                        outerRadius={80}
                        fill="#8884d8"
                        dataKey="value"
                        onMouseEnter={onPieEnter}
                        />
                    </PieChart>
                    </ResponsiveContainer>
                </div>
            }
            </div>
        {/* TET = Token Events and TimeLine */}
        <div className='TD_TETContainer' style={{height:isMobile?'500px':'327px'}}>
            <div className='TD_TET_TokenEvent'>
                {
                    !isMobile && <div className='TD_TET_TokenEvent_Token'>
                    <span>Contract Address:</span>
                    <span>{Data.TokenInfo.contractAddress}</span>
                    <span>Total Supply:</span>
                    <span>{Data.TokenInfo.totalSupply}</span>
                    <span>Decimals : {Data.TokenInfo.decimals}</span>
                </div>
                }
                <div className='TD_TET_TokenEvent_Event' style={{width:isMobile?'100%' : '96%', height:isMobile?'100%' : 'auto'}}>
                    {
                        selectedPhase == "launch" && 
                        <button style={{
                            opacity:Data.TokenInfo.chartLink == ""?
                            "0.4" : "1",
                            pointerEvents:Data.TokenInfo.chartLink == ""?
                            "none" : "all",
                            fontSize:isMobile? '70px' : '25px'
                        }} className='TD_eventLink'>
                            <a style={{color:'rgb(184, 197, 255)'}} href={
                                !Data.TokenInfo.chartLink.includes("https://")?
                                "https://" +  Data.TokenInfo.chartLink : Data.TokenInfo.chartLink
                            }>
                                Chart
                            </a>
                        </button>
                    }
                    <button className='TD_eventLink' style={{
                            opacity:Data.EventInfo[selectedPhase + "_link"] == ""?
                            "0.4" : "1",
                            pointerEvents:Data.EventInfo[selectedPhase + "_link"] == ""?
                            "none" : "all",
                            fontSize:isMobile? '70px' : '25px'
                        }}>
                            <a style={{color:'rgb(184, 197, 255)'}} href={
                                !Data.EventInfo[selectedPhase + "_link"].includes("https://")?
                                "https://" +  Data.EventInfo[selectedPhase + "_link"] : Data.EventInfo[selectedPhase + "_link"]
                            }>
                               {selectedPhase}  Link
                            </a>
                        </button>
                    {
                        Array(Object.keys(getDetails(selectedPhase)).length).fill().map((_, i)=>{
                            let details = getDetails(selectedPhase);
                            let edk = Object.keys(details);
                            return <div className='TD_TET_TokenEvent_Event_Box'>
                                    <span style={{color:'rgb(184, 197, 255)', fontSize:isMobile? '70px' : '25px'}}>{edk[i]}</span>
                                    <span style={{fontSize:isMobile? '70px' : '23px'}}>{details[edk[i]]}</span>
                            </div>
                        })
                    }
                </div>      
            </div>
            <div className='TD_TimeLine_Texts'>
                <span id={'whitelist_dot_text_' + Data.projectTag}>Whitelist</span>
                <span id={'presale_dot_text_' + Data.projectTag}>Presale</span>
                <span id={'launch_dot_text_' + Data.projectTag}>Launch</span>
            </div>
            <div className='TD_Timeline_Container'>
                <div className='TD_timeline_dotDiv'>
                    <span id={'whitelist_dot_' + Data.projectTag} onClick={event_dot_clicked}></span>
                    <span id={'presale_dot_' + Data.projectTag} onClick={event_dot_clicked}></span>
                    <span id={'launch_dot_' + Data.projectTag} onClick={event_dot_clicked}></span>
                </div>
                <span className='TD_TimeLine_Line'></span>
            </div>
        </div>
        {
            Data.EventInfo.eventDetails != "" &&
            <div className='TD_EventDetails'>
                <span>Event Details: </span>
                <span>{Data.EventInfo.eventDetails}</span>
            </div>

        }
        <div className='TD_ShortAudit'>
            <span className='TD_ShortAudit_Title'>Short Audit:</span>
            {Array(setAudit().length).fill().map((_, i)=>{
                return <div>
                        {setAudit()[i]}
                    </div>
            })}
        </div>
    </div>
  )
}

export default TokenDetails;