import storageHandler from "./storageHandler";
const sh = storageHandler();

const notifInitial = {
    show : false,
    top : 0,
    left : 0,
    x : 0,
    y : 0,
    message: null,
    type: null,
    Buttons : null,
    ButtonsFunc: null,
    Costumize : false }

const notifReducer = (state = notifInitial, action) => {
    let newState = {...state};
    switch(action.type){
        case "showNotif":       
            newState = {
                show : action.value.show,
                top : action.value.top,
                left : action.value.left,
                x : action.value.x,
                y : action.value.y,
                message: action.value.message,
                type: action.value.type,
                Buttons : action.value.Buttons,
                ButtonsFunc : action.value.ButtonsFunc,
                Costumize : action.value.Costumize
            }
            break;
        case "hideNotif":
            newState = action.value;
            break;
        default:
            return state;
    }
    state = newState;
    // console.log("this is the state"+ JSON.stringify(state));
    return state;
}

const AddProjectReducer = (state = {}, action) => {
    let PDB = sh.loadFrom("ProjectsDB");
    if(PDB == undefined){
        PDB = {};
    }
    let len = Object.keys(state).length;
    switch(action.type){
        case "AddProject":
            PDB["Project_"+len] = action.value;
            PDB["Project_"+len]["projectTag"] = "Project_"+len;
            // console.log('Project DB: ' + JSON.stringify(PDB));
            break;
        default:
            return state;
    }
    state = PDB;
    sh.saveTo("ProjectsDB", state);
    // console.log("current projects" + JSON.stringify(state))
    return state;
}

const formReducer = (state = {}, action) =>{
    switch(action.type){
        case "updateForm" : 
            state[action.formName] = action.value;
            break;
        default:
            return state;
    }
    return state;
}

const CurrentFormReducer = (state = 0, action)=>{
    switch(action.type){
        case "nextForm" :
            state = state  == 6 ? state : state + 1;
            break;
        case "prForm" : 
            state = (state > 0) ? state - 1 : state;
            break;
        case "setIndex":
            state = action.value;
            break;
    }
    return state;
}

const currentProjectReducer = (state = null, action)=>{
    switch(action.type){
        case "select":
            state = action.project;
            break;
        default:
            break;
    }
    return state;
}

const promotedsReducer = (state = [], action)=>{
    let promoteds = sh.loadFrom("Promoteds");
    if(promoteds == undefined){
        promoteds = [];
    }
    switch(action.type){
        case "promote" :
            promoteds.push(action.value);
            break;
        case "unpromote":
            promoteds.splice(action.valueIndex, 1);
            break;
    }
    state = promoteds;
    sh.saveTo("Promoteds", state);
    // console.log('promoteds ' + JSON.stringify(promoteds));
    return state;
}

const IsPhoneReducer = (state = false, action)=>{
    switch(action.type){
        case "Phone" : 
            state = true;
            break;
        case "Desktop":
            state = false;
            break;
        default:
            return state;
    }
    return state;
}

const reducers = {
    notifShow : notifReducer,
    AddProject : AddProjectReducer,
    form : formReducer,
    currentForm : CurrentFormReducer,
    currentProject : currentProjectReducer,
    promotedsReducer : promotedsReducer,
    IsPhoneReducer : IsPhoneReducer
}

export default reducers;