import React from 'react'
import {useDispatch, useSelector, shallowEqual} from 'react-redux';
import "./Plans.css";

function Plans() {
  const cf = useSelector(state=>state.cf, shallowEqual);
  const dispatch = useDispatch();
  let promoteds = useSelector(state=>state.promoteds, shallowEqual);

  let plans = {
    "Silver" : {
      "Options" : ["Add To Tokens List"],
      "Price" : 20,
      "Color" : 'rgb(184, 197, 255)'
    },
    "Golden" : {
      "Options" : ["Add To Tokens List", "Full Review On Contract"],
      "Price" : 30,
      "Color" : 'gold'
    },
    "Diamond" : {
      "Options" : ["Add To Tokens List", "Full Review On Contract", "Promoted"],
      "Price" : 50,
      "Color" : "blue"
    }
  }

  const handleIndex = (event)=>{
    switch(event.target.name){
        case "NextDot":
          if(dispatch_data(event.target.id) == true){
            dispatch({type:'nextForm'});
          } 
            break;
        case "PrevDot":
            dispatch({type:'prForm'})
            break;
    }
  }
  
  const dispatch_data = (id)=>{
    if(id == "Diamond" && promoteds.length >= 5){
      alert('Sorry! Only 5 Projects Can Be Promoted On Website, Please Try This Plan Later Or Contact Us To Reserve A Spot!');
      return false;
    }
    let finalData = {
      "name" : id,
      "value" : plans[id]['Price']
    }
    dispatch({type:'updateForm', formName : "Plans", value:finalData});
    return true;
}

  return (
    <div className='P_mainDiv'>
      <div className='P_formContainer'>
        {Array(Object.keys(plans).length).fill().map((_, i)=>{
          let planTitles = Object.keys(plans);
          return <div style={{borderColor:plans[planTitles[i]]['Color'], justifyContent:'space-between'}}>
            <span className='P_planTitle' style={{color : plans[planTitles[i]]['Color']}}>{planTitles[i]}</span>
            <div className='P_planContent' style={{border:'none'}}>
                {Array(plans[planTitles[i]]['Options'].length).fill().map((_, i)=>{
                  return <span className='P_planText'>- {plans[planTitles[i]]['Options'][i]}</span>
                })}
            </div>
            <div className='P_planPrice' style={{padding:'0px', border:'none', flexDirection:'row'}}>
              <span style={{color : plans[planTitles[i]]['Color']}}>Price: </span>
              <span style={{color : plans[planTitles[i]]['Color']}}> {plans[planTitles[i]]['Price']}$</span>
            </div>
            <button className='P_planButton' style={{color : plans[planTitles[i]]['Color'], borderColor:plans[planTitles[i]]['Color']}} name='NextDot' id={planTitles[i]} onClick={handleIndex}>Select</button>
          </div>
        })}
      </div>
      <div style={{width:'100%', display:'flex', justifyContent:'center', marginTop:'20px'}}>
        <button className='P_Buttons' name='PrevDot' onClick={handleIndex}>Back</button>
      </div>
    </div>
  )
}

export default Plans